(200, 205, 210, 0.3);\n  }\n\n  ::-webkit-scrollbar-thumb {\n    background-color: rgb(200, 205, 210);\n  }\n\n  ::-webkit-scrollbar-track,\n  ::-webkit-scrollbar-thumb {\n    border-radius: 8px;\n  }\n}\n\n.icon,\nbutton {\n  user-select: none;\n}\n\nbutton {\n  cursor: pointer;\n}\n\n[text-wrap='off'] {\n  text-wrap: nowrap;\n}\n\n[overflow='ellipsis'] {\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n#main-title {\n  font-size: 3rem;\n  font-weight: 600;\n  padding: 16px 32px;\n  border-radius: 8px;\n  width: 100%;\n  text-align: center;\n  margin: 10px 0 20px 0;\n  filter: var(--drop-shadow-smoother);\n\n  display: flex;\n  justify-content: center;\n  gap: 12px;\n\n  .logo {\n    width: 4.25rem;\n  }\n\n  .text {\n    font-family: 'Coiny', var(--system-ui);\n    font-weight: 100;\n  }\n}\n\n.container {\n  max-width: 1300px;\n}\n\n#header {\n  display: grid;\n  grid-template-columns: 1fr 1fr 1fr;\n  gap: 16px;\n  padding: 16px;\n}\n\n#location-info {\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n  filter: var(--drop-shadow);\n  justify-self: start;\n\n  .current-location {\n    display: flex;\n    align-items: center;\n    gap: 4px;\n    font-size: 1.2rem;\n    line-height: 1.2rem;\n    font-weight: 600;\n    max-width: 32ch;\n    text-transform: capitalize;\n  }\n\n  #query-form {\n    display: flex;\n    align-items: center;\n\n    input {\n      border: none;\n      background-color: transparent;\n      border-bottom: 2px solid white;\n      outline: none;\n      color: inherit;\n\n      &::placeholder {\n        color: var(--placeholder-color);\n      }\n    }\n\n    button {\n      outline: none;\n      border: none;\n      background-color: transparent;\n    }\n\n    .loading {\n      margin-left: 10px;\n      font-weight: 500;\n    }\n  }\n\n  .error {\n    color: #fff;\n    max-width: 380px;\n  }\n}\n\n#main-info {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  font-weight: 600;\n  text-align: center;\n  justify-self: center;\n  filter: var(--drop-shadow-smooth);\n\n  .wrapper {\n    display: flex;\n    align-items: center;\n    gap: 16px;\n\n    .icon {\n      width: 5rem;\n    }\n\n    .temperature {\n      font-size: 5rem;\n    }\n  }\n\n  .feels-like {\n    color: var(--font-color-secondary);\n  }\n\n  .conditions {\n    max-width: 32ch;\n    text-transform: capitalize;\n  }\n}\n\n#settings {\n  justify-self: end;\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n  filter: var(--drop-shadow-smoother);\n\n  .setting {\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n    font-size: 0.9rem;\n\n    .label {\n      font-weight: 500;\n    }\n\n    .options {\n      display: flex;\n      gap: 12px;\n\n      button {\n        outline: 2px solid transparent;\n        border-radius: 4px;\n        border: none;\n        width: 10ch;\n        color: #333;\n\n        &[selected] {\n          background-color: var(--button-selected-color);\n        }\n\n        &:hover {\n          outline-color: #9ab1c4;\n        }\n      }\n    }\n  }\n}\n\n.cards-section {\n  flex: 1;\n  display: grid;\n  margin-top: 32px;\n  grid-template-columns: 1fr 180px 180px 1fr;\n  grid-template-rows: 1fr 180px 180px;\n  gap: 24px;\n\n  .card {\n    display: flex;\n    flex-direction: column;\n    padding: 16px;\n\n    .title {\n      text-transform: uppercase;\n      font-weight: 600;\n      color: var(--font-color-card-title);\n      border-bottom: 1px solid #c6d9e27e;\n      padding-bottom: 4px;\n    }\n  }\n}\n\n#hourly-forecast {\n  grid-column: 1 / -2;\n  grid-row-start: 1;\n  padding-bottom: 24px;\n\n  .title {\n    margin-bottom: 32px;\n  }\n\n  .content {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n    height: 100%;\n    padding: 8px 16px;\n\n    .today-info {\n      .subtitle {\n        font-size: 1.15rem;\n        font-weight: 600;\n        margin-bottom: 8px;\n      }\n\n      .min-max {\n        margin-bottom: 16px;\n      }\n\n      .value,\n      .temperature-unit {\n        font-weight: 600;\n      }\n    }\n\n    .hours {\n      display: grid;\n      grid-auto-flow: column;\n      grid-template-rows: 1fr 2fr 2fr 1fr;\n      grid-auto-columns: 50px;\n      align-items: center;\n      justify-items: center;\n      gap: 0px 24px;\n      overflow-x: scroll;\n      font-size: 0.9rem;\n      padding-bottom: 8px;\n\n      .time {\n        font-weight: 500;\n      }\n\n      .wrapper {\n        display: flex;\n        color: var(--font-color-secondary);\n        font-size: 0.8rem;\n      }\n\n      .icon {\n        width: 35px;\n      }\n    }\n  }\n}\n\n#day-forecast {\n  grid-row: 1 / -1;\n  grid-column-start: -2;\n\n  .title {\n    margin-bottom: 32px;\n  }\n\n  .content {\n    display: grid;\n    grid-template-columns: 2fr 1fr 2fr 2fr;\n    grid-auto-rows: minmax(3rem, auto);\n    gap: 8px;\n    justify-items: center;\n    align-items: center;\n\n    .description {\n      grid-row-start: 1;\n      grid-column: 1 / -1;\n      font-size: 0.95rem;\n      padding: 0 32px;\n    }\n\n    .icon {\n      width: 35px;\n    }\n\n    .value,\n    .temperature-unit {\n      font-weight: 600;\n    }\n  }\n}\n\n#precipitation {\n  grid-column-start: 1;\n  grid-row-start: 2;\n\n  .content {\n    display: grid;\n    height: 100%;\n    grid-template-columns: 1fr 1.25fr 1.25fr;\n    gap: 8px;\n    align-items: center;\n    justify-items: center;\n    padding: 8px;\n    font-size: 1.5rem;\n\n    .icon {\n      width: 70px;\n    }\n\n    .precip-level,\n    .precip-prob {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n    }\n\n    .value {\n      font-size: 2.25rem;\n      font-weight: 600;\n    }\n\n    .feedback {\n      color: var(--font-color-secondary);\n      font-size: 1rem;\n    }\n  }\n}\n\n#wind {\n  grid-column-start: 1;\n  grid-row-start: 3;\n\n  .content {\n    display: flex;\n    align-items: center;\n    padding: 16px 32px;\n    height: 100%;\n    gap: 64px;\n    font-size: 1rem;\n\n    .wrapper {\n      display: grid;\n      grid-template-rows: 1fr 1fr 1fr;\n      align-items: center;\n      gap: 4px;\n    }\n\n    .value {\n      font-size: 1.25rem;\n    }\n\n    .feedback {\n      color: var(--font-color-secondary);\n    }\n  }\n\n  .compass {\n    border: 2px dashed #fff;\n    width: 80px;\n    height: 80px;\n    border-radius: 50%;\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    .north {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      font-family: sans-serif;\n      font-size: 13px;\n      line-height: 0;\n      background-color: #fff;\n      color: #7c7c7c;\n      font-weight: 600;\n      padding: 10px;\n      position: absolute;\n      border-radius: 50%;\n      border: 1px solid white;\n      left: 38%;\n      top: -8px;\n      width: 2ch;\n      height: 2ch;\n    }\n\n    #arrow-icon {\n      transform: rotate(0deg);\n      width: 40px;\n    }\n  }\n}\n\n#humidity,\n#pressure,\n#sunrise-sunset,\n#uv-index {\n  .content {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    height: 100%;\n  }\n}\n\n#sunrise-sunset {\n  grid-column-start: 2;\n  grid-row-start: 2;\n\n  .content {\n    gap: 8px;\n    padding: 16px;\n\n    .label {\n      font-size: 0.9rem;\n    }\n\n    .value {\n      font-size: 1.15rem;\n      font-weight: 600;\n    }\n  }\n}\n\n#humidity {\n  grid-column-start: 3;\n  grid-row-start: 2;\n\n  .content {\n    align-items: center;\n\n    .air-humidity {\n      font-size: 1.5rem;\n      .value {\n        font-size: 2.5rem;\n        font-weight: 600;\n      }\n    }\n\n    .feedback {\n      color: var(--font-color-secondary);\n    }\n  }\n}\n\n#pressure {\n  grid-column-start: 2;\n  grid-row-start: 3;\n\n  .content {\n    align-items: center;\n\n    .air-pressure {\n      font-size: 1.2rem;\n      .value {\n        font-size: 2rem;\n        font-weight: 600;\n      }\n    }\n\n    .feedback {\n      color: var(--font-color-secondary);\n    }\n  }\n}\n\n#uv-index {\n  grid-column-start: 3;\n  grid-row-start: 3;\n\n  .content {\n    align-items: center;\n\n    .value {\n      font-size: 2rem;\n      font-weight: 600;\n    }\n\n    .feedback {\n      color: var(--font-color-secondary);\n    }\n\n    .meter {\n      border-radius: 8px;\n      width: calc(calc(11 * 10px) + 4px);\n      background-color: #fff;\n      height: 10px;\n      padding: 2px;\n      margin-top: 8px;\n\n      .bar {\n        border-radius: inherit;\n        height: 100%;\n        width: 0%;\n        background-color: #fff;\n      }\n    }\n  }\n}\n\n.footer {\n  padding: 2px 0;\n  margin: 20px 0 4px 0;\n  filter: var(--drop-shadow);\n\n  a {\n    text-decoration: none;\n    color: inherit;\n  }\n}\n"],"sourceRoot":""}]);
